.App {
  text-align: center;
}

.ToDoList{
  margin: 4rem auto;
  padding: 2rem 3rem 3rem;
  max-width: 500px;
  background: #ff6666;
  color: #fff;
  box-shadow: -20px -20px 0px 0px rgba(100,100,100,0.1);
}

.ToDoList ul {
  list-style: none;
  margin-top: 1rem ;
}

.ToDoList h1 {
  letter-spacing: 0.2rem;
  border-bottom: 1px solid rgba(255,255,255,0.3);
}

.ToDo{
  display: flex;
  margin: 0 -3rem 4px;
  padding: 1.1rem 3rem;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.1);
}

.ToDo-Task {
  position: relative;
  transition: opacity 0.4s linear;
}

.ToDo-Task.Completed{
  opacity: 0.6;
}

.ToDo-Task.Completed:before{
  content: "";
  position: absolute;
  top: 50%;
  left: -0.5rem;
  display: block;
  width: 0%;
  height: 3px;
  background: #fff;
  animation: si 0.5s ease-out 0s forwards;
}
.ToDo-Task.Activated:before{
  content: "";
  position: absolute;
  top: 50%;
  left: -0.5rem;
  display: block;
  width: calc(100% + 1rem);
  height: 3px;
  background: #fff;
  animation: so 0.5s ease-out 0s forwards;
}

@keyframes si {
  to{
    width: calc(100% + 1rem);
  }
}

@keyframes so {
  to{
    width: 0%;
  }
}

.ToDo-Btn-Group{
  flex-shrink: 0;
  padding-left:0.7rem;
}

.ToDo-Btn-Group button{
  border: none;
  font-size: 1em;
  margin: 0.4rem;
  background: none;
  -webkit-appearance: none;
  cursor: pointer;
  color: #fff;
  outline: none;
}

.todo-edit-form{
  display: flex;
  flex-wrap: wrap;
}
.todo-edit-form label{
 min-width: 100%;
 margin-bottom: 0.5rem ;
 font-size: 1.3rem;
}
.todo-edit-form input {
  flex-grow: 1;
  border: none;
  background: #f7f7f1;
  padding: 0 1rem;
  font-size: initial;
}

.todo-edit-form button{
  padding: 0.5rem 1rem;
  border: none;
  background-color: #ff6666;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  border: 1px solid rgba(255, 255, 255, 0.4);
  margin-left: 5px;
  transition:  background-color 0.2s ease-out;
  }

.todo-edit-form button:hover{
  background-color: #ff5a5a;
  cursor: pointer;
  }

